  import { getCarryTicketList, getTicket } from '@/api/user.js'
  import noData from '@/components/noData.vue'
  export default {
    name: 'receiveCoupon',
    components: {
      noData
    },
    data () {
      return {
        isShow: false,
        listData: {'img': 'static/images/stroke.png', 'text': '您还没领到优惠券哦~'},
        ticketlist: [],
        showtoast: false,
        checkid: ''
      }
    },
    computed: {
      ticketlistFilter () {
        return this.ticketlist.filter(item => {
          return item.usagetype !== '1'
        })
      }
    },
    methods: {
      couponList () {
        const that = this
        const postData = {
          id: that.wantid
        }
        getCarryTicketList(postData)
          .then(res => {
            if (res.data.errorcode === 0) {
              that.ticketlist = res.data.data.ticketlist
            } else {
              this.$vux.toast.text(res.data.errormsg, 'middle')
            }
          })
          .catch(err => {
            this.$vux.toast.text('网络异常！', 'middle')
          })
      },
      checkCoupon (checkid) {
        this.showtoast = true
        this.checkid = checkid
      },
      noshow () {
        this.showtoast = false
      },
      defaulsure () {
        this.showtoast = false
        const that = this
        const postData = {
          id: this.checkid
        }
        getTicket(postData)
              .then(res => {
                if (res.data.errorcode === 0) {
                  this.$vux.toast.text('领取成功', 'middle')
                  this.couponList()
                } else {
                  this.$vux.toast.text(res.data.errormsg, 'middle')
                }
              })
              .catch(err => {
                this.$vux.toast.text('网络异常！', 'middle')
              })
      }
    },
    created () {
      this.couponList()
}
  }



